.button {
    display: flex;
    align-items: center;
    text-align: center;
    border: 1px solid #ffffff88;
    padding-left: 10px;
    padding-right: 10px;
    text-decoration: none;
    user-select: none;
    color: black;
    height: 100%;
}

.button:hover {
    background-color: #ffffff33;
}